body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    color: #666666;
    font-family: "Open Sans", sans-serif;
    overflow-x: hidden;
    background-color: #ffcccc
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
}

.calc-wrapper {
    width: 400px;
    height: 600px;
}

.row {
    display: flex;
    width: 100%;
}

.header{
    display: inline-flex;
    float: left;
    text-align: center;
}

.list{
    display: inline-flex;
    text-align: center;
}

ul li{
    list-style: none;
}
.button {
    display: flex;
    height: 3em;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #DDDCDC;
    color: #888;
    outline: 1px solid #888;
}

.operator {
    background-color: #462B26;
    color: #fff;
}
.input {
    display: flex;
    height: 2em;
    flex: 1 1;
    background-color: #000;
    outline: 1px solid #888;
    color: #fff;
    justify-content: flex-end;
    padding: 0.5em;
    font-weight: bold;
    font-size: 1.2em;
    align-items: center;
}
.clear-btn {
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 1.1em;
    outline: 1px solid #888;
    height: 3.8em;
    background-color: #462B26;
}
